@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.App{text-align:center}:root{--primary-white: #ffffff;--primary-black: #343a40;--primary-grey: #666666;--primary-red: #f44242}body{font-family:"Roboto", sans-serif}.logo{width:2rem}.navbar .navbar-nav>li>a{color:#ffffff !important;color:var(--primary-white) !important;border-top:3px solid transparent}.navbar .navbar-nav>li>a.active,.navbar .navbar-nav>li>a:hover{border-top:3px solid;border-color:#f44242;border-color:var(--primary-red)}.navbar .navbar-collapse.show{z-index:9}.page-content{min-height:85vh;overflow-y:auto;position:relative}.footer{color:#ffffff;color:var(--primary-white);background-color:#666666;background-color:var(--primary-grey);display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.laptop-image-wrapper{font-size:12px;position:relative}.laptop-image-wrapper .laptop-image{width:100%}.laptop-image-wrapper .typed-content{position:absolute;left:18%;top:10%;width:66%;font-weight:700}.laptop-image-wrapper .typed-content .typed-cursor{display:none}.laptop-image-wrapper p{margin-bottom:0.65rem}.page-content .container #tsparticles{position:absolute;width:100%;top:0;left:0;height:70vh}.page-content .container .header-wrapper{min-height:70vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.share-buttons-wrapper{position:absolute;left:0;z-index:9;top:30%}.share-buttons-wrapper a{color:#ffffff;color:var(--primary-white);padding:0.1rem 0.5rem;font-size:2rem;background:#343a40;background:var(--primary-black);border-radius:0 1.5rem 1.5rem 0}.share-buttons-wrapper a:hover{color:#f44242;color:var(--primary-red);padding-left:1rem}.share-buttons-wrapper .linkedin-icon a{padding-right:0.7rem}.share-buttons-wrapper .envelope-icon a{font-size:1.8rem;padding-right:0.7rem}@media (min-width: 425px){.laptop-image-wrapper{font-size:16px}.laptop-image-wrapper p{margin-bottom:1rem}}@media (min-width: 768px){.laptop-image-wrapper{font-size:24px}}@media (min-width: 769px){.navbar{height:10vh}.logo{width:3rem}.page-content{height:85vh}.page-content .container #tsparticles{height:85vh}.page-content .container .header-wrapper{height:85vh}.footer{height:5vh}.laptop-image-wrapper .typed-content{font-size:1.5rem}}.aboutme-wrapper{background-color:#343a40;background-color:var(--primary-black);color:#ffffff;color:var(--primary-white)}.experience-wrapper .experience-img-wrapper{display:-webkit-flex;display:flex;height:100%;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.experience-wrapper .experience-img-wrapper img{width:100%}

